'use client';

import { useState, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import * as Dialog from '@radix-ui/react-dialog';
import {
  X,
  Award,
  Loader2,
  Calendar,
  Building,
  Link as LinkIcon,
  Hash,
  Upload,
  Trash2,
  CheckCircle,
} from 'lucide-react';
import { Button } from '@/components/ui/Button';
import { createCertificate } from '@/lib/api/profile';
import { uploadCertificate } from '@/lib/api/posts';
import type { Certificate, CertificateInput } from '@/types/profile';

interface AddCertificateModalProps {
  isOpen: boolean;
  onClose: () => void;
  onCertificateAdded: (certificate: Certificate) => void;
}

export function AddCertificateModal({
  isOpen,
  onClose,
  onCertificateAdded,
}: AddCertificateModalProps) {
  const [loading, setLoading] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Form state
  const [name, setName] = useState('');
  const [issuingOrg, setIssuingOrg] = useState('');
  const [issueDate, setIssueDate] = useState('');
  const [expiryDate, setExpiryDate] = useState('');
  const [doesNotExpire, setDoesNotExpire] = useState(false);
  const [credentialId, setCredentialId] = useState('');
  const [credentialUrl, setCredentialUrl] = useState('');
  const [certificateFile, setCertificateFile] = useState<File | null>(null);
  const [certificatePreview, setCertificatePreview] = useState<string | null>(null);
  const [uploadMethod, setUploadMethod] = useState<'url' | 'file'>('file');

  const fileInputRef = useRef<HTMLInputElement>(null);

  const resetForm = () => {
    setName('');
    setIssuingOrg('');
    setIssueDate('');
    setExpiryDate('');
    setDoesNotExpire(false);
    setCredentialId('');
    setCredentialUrl('');
    setCertificateFile(null);
    setCertificatePreview(null);
    setUploadMethod('file');
    setError(null);
  };

  const handleClose = () => {
    resetForm();
    onClose();
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      // Validate file type
      if (!file.type.startsWith('image/')) {
        setError('Please select an image file');
        return;
      }
      // Validate file size (max 5MB)
      if (file.size > 5 * 1024 * 1024) {
        setError('File size should be less than 5MB');
        return;
      }

      setCertificateFile(file);
      setCertificatePreview(URL.createObjectURL(file));
      setError(null);
    }
  };

  const handleRemoveFile = () => {
    if (certificatePreview) {
      URL.revokeObjectURL(certificatePreview);
    }
    setCertificateFile(null);
    setCertificatePreview(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!name.trim()) {
      setError('Certificate name is required');
      return;
    }
    if (!issuingOrg.trim()) {
      setError('Issuing organization is required');
      return;
    }
    if (!issueDate) {
      setError('Issue date is required');
      return;
    }
    if (!doesNotExpire && expiryDate && new Date(expiryDate) < new Date(issueDate)) {
      setError('Expiry date cannot be before issue date');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      let finalCredentialUrl = credentialUrl.trim();

      // Upload certificate file if selected
      if (uploadMethod === 'file' && certificateFile) {
        setUploading(true);
        try {
          const result = await uploadCertificate(certificateFile);
          finalCredentialUrl = result.certificateUrl;
        } catch (uploadErr: any) {
          console.error('Failed to upload certificate:', uploadErr);
          setError('Failed to upload certificate image. Please try again.');
          setLoading(false);
          setUploading(false);
          return;
        }
        setUploading(false);
      }

      const certificateData: CertificateInput = {
        name: name.trim(),
        issuingOrg: issuingOrg.trim(),
        issueDate,
        expiryDate: doesNotExpire ? undefined : expiryDate || undefined,
        doesNotExpire,
        credentialId: credentialId.trim() || undefined,
        credentialUrl: finalCredentialUrl || undefined,
      };

      const certificate = await createCertificate(certificateData);
      onCertificateAdded(certificate);
      handleClose();
    } catch (err: any) {
      console.error('Failed to add certificate:', err);
      setError(err.response?.data?.error || err.message || 'Failed to add certificate');
    } finally {
      setLoading(false);
      setUploading(false);
    }
  };

  return (
    <Dialog.Root open={isOpen} onOpenChange={(open) => !open && handleClose()}>
      <AnimatePresence>
        {isOpen && (
          <Dialog.Portal forceMount>
            <Dialog.Overlay asChild>
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50"
              />
            </Dialog.Overlay>
            <Dialog.Content asChild>
              <motion.div
                initial={{ opacity: 0, scale: 0.95, y: 20 }}
                animate={{ opacity: 1, scale: 1, y: 0 }}
                exit={{ opacity: 0, scale: 0.95, y: 20 }}
                transition={{ type: 'spring', duration: 0.3 }}
                className="fixed left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-lg max-h-[90vh] overflow-y-auto bg-white dark:bg-neutral-900 rounded-2xl shadow-2xl z-50 border border-gray-200 dark:border-neutral-800"
              >
                {/* Header */}
                <div className="sticky top-0 bg-white dark:bg-neutral-900 border-b border-gray-200 dark:border-neutral-800 px-6 py-4 flex items-center justify-between">
                  <Dialog.Title className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                    <Award className="w-5 h-5 text-amber-400" />
                    Add Certification
                  </Dialog.Title>
                  <Dialog.Close asChild>
                    <button
                      className="p-2 hover:bg-gray-100 dark:hover:bg-neutral-800 rounded-lg transition-colors"
                      aria-label="Close"
                    >
                      <X className="w-5 h-5 text-gray-500 dark:text-neutral-400" />
                    </button>
                  </Dialog.Close>
                </div>

                {/* Form */}
                <form onSubmit={handleSubmit} className="p-6 space-y-5">
                  {/* Error Message */}
                  {error && (
                    <div className="p-3 bg-red-500/10 border border-red-500/30 rounded-lg text-red-400 text-sm">
                      {error}
                    </div>
                  )}

                  {/* Certificate Name */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      Certificate Name *
                    </label>
                    <input
                      type="text"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      placeholder="e.g., AWS Solutions Architect, Google Cloud Professional"
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                    />
                  </div>

                  {/* Issuing Organization */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <Building className="w-4 h-4 inline mr-1" />
                      Issuing Organization *
                    </label>
                    <input
                      type="text"
                      value={issuingOrg}
                      onChange={(e) => setIssuingOrg(e.target.value)}
                      placeholder="e.g., Amazon Web Services, Google, Microsoft"
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                    />
                  </div>

                  {/* Issue Date */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <Calendar className="w-4 h-4 inline mr-1" />
                      Issue Date *
                    </label>
                    <input
                      type="date"
                      value={issueDate}
                      onChange={(e) => setIssueDate(e.target.value)}
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                    />
                  </div>

                  {/* Expiry Date */}
                  <div>
                    <div className="flex items-center justify-between mb-2">
                      <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300">
                        <Calendar className="w-4 h-4 inline mr-1" />
                        Expiry Date
                      </label>
                      <label className="flex items-center gap-2 text-sm text-gray-600 dark:text-neutral-400 cursor-pointer">
                        <input
                          type="checkbox"
                          checked={doesNotExpire}
                          onChange={(e) => setDoesNotExpire(e.target.checked)}
                          className="w-4 h-4 rounded border-gray-300 dark:border-neutral-600 text-blue-600 focus:ring-blue-500"
                        />
                        <CheckCircle className="w-4 h-4 text-emerald-400" />
                        Does not expire
                      </label>
                    </div>
                    <input
                      type="date"
                      value={expiryDate}
                      onChange={(e) => setExpiryDate(e.target.value)}
                      disabled={doesNotExpire}
                      className={`w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500 ${
                        doesNotExpire ? 'opacity-50 cursor-not-allowed' : ''
                      }`}
                    />
                  </div>

                  {/* Credential ID */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <Hash className="w-4 h-4 inline mr-1" />
                      Credential ID (Optional)
                    </label>
                    <input
                      type="text"
                      value={credentialId}
                      onChange={(e) => setCredentialId(e.target.value)}
                      placeholder="e.g., ABC123XYZ"
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                    />
                  </div>

                  {/* Certificate Upload */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <LinkIcon className="w-4 h-4 inline mr-1" />
                      Certificate/Credential (Optional)
                    </label>

                    {/* Upload Method Toggle */}
                    <div className="flex gap-2 mb-3">
                      <button
                        type="button"
                        onClick={() => setUploadMethod('file')}
                        className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium transition-all ${
                          uploadMethod === 'file'
                            ? 'bg-blue-600 text-white'
                            : 'bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400 hover:bg-gray-200 dark:hover:bg-neutral-700'
                        }`}
                      >
                        <Upload className="w-4 h-4 inline mr-1" />
                        Upload Image
                      </button>
                      <button
                        type="button"
                        onClick={() => setUploadMethod('url')}
                        className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium transition-all ${
                          uploadMethod === 'url'
                            ? 'bg-blue-600 text-white'
                            : 'bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400 hover:bg-gray-200 dark:hover:bg-neutral-700'
                        }`}
                      >
                        <LinkIcon className="w-4 h-4 inline mr-1" />
                        Paste URL
                      </button>
                    </div>

                    {uploadMethod === 'file' ? (
                      <div>
                        {certificatePreview ? (
                          <div className="relative rounded-lg overflow-hidden border border-gray-200 dark:border-neutral-700">
                            <img
                              src={certificatePreview}
                              alt="Certificate preview"
                              className="w-full max-h-48 object-contain bg-gray-50 dark:bg-neutral-800"
                            />
                            <button
                              type="button"
                              onClick={handleRemoveFile}
                              className="absolute top-2 right-2 p-2 bg-red-500 hover:bg-red-600 text-white rounded-full transition-colors"
                            >
                              <Trash2 className="w-4 h-4" />
                            </button>
                            <div className="absolute bottom-2 left-2 px-2 py-1 bg-black/60 text-white text-xs rounded">
                              {certificateFile?.name}
                            </div>
                          </div>
                        ) : (
                          <div
                            onClick={() => fileInputRef.current?.click()}
                            className="border-2 border-dashed border-gray-300 dark:border-neutral-600 rounded-lg p-6 text-center cursor-pointer hover:border-blue-500 dark:hover:border-blue-500 transition-colors"
                          >
                            <Upload className="w-8 h-8 mx-auto text-gray-400 dark:text-neutral-500 mb-2" />
                            <p className="text-sm text-gray-600 dark:text-neutral-400">
                              Click to upload certificate image
                            </p>
                            <p className="text-xs text-gray-400 dark:text-neutral-500 mt-1">
                              PNG, JPG up to 5MB
                            </p>
                          </div>
                        )}
                        <input
                          ref={fileInputRef}
                          type="file"
                          accept="image/*"
                          onChange={handleFileSelect}
                          className="hidden"
                        />
                      </div>
                    ) : (
                      <input
                        type="url"
                        value={credentialUrl}
                        onChange={(e) => setCredentialUrl(e.target.value)}
                        placeholder="https://..."
                        className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                      />
                    )}
                  </div>

                  {/* Submit Button */}
                  <div className="flex justify-end gap-3 pt-4">
                    <Button
                      type="button"
                      onClick={handleClose}
                      disabled={loading || uploading}
                      className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
                    >
                      Cancel
                    </Button>
                    <Button
                      type="submit"
                      disabled={loading || uploading}
                      className="bg-blue-600 hover:bg-blue-700 text-white"
                    >
                      {uploading ? (
                        <>
                          <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                          Uploading...
                        </>
                      ) : loading ? (
                        <>
                          <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                          Adding...
                        </>
                      ) : (
                        'Add Certificate'
                      )}
                    </Button>
                  </div>
                </form>
              </motion.div>
            </Dialog.Content>
          </Dialog.Portal>
        )}
      </AnimatePresence>
    </Dialog.Root>
  );
}
