'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import * as Dialog from '@radix-ui/react-dialog';
import {
  Trophy,
  Plus,
  ExternalLink,
  Calendar,
  Medal,
  Target,
  Award,
  Sparkles,
  Gift,
  Edit2,
  X,
  Building,
  FileText,
  Eye,
} from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import type { Achievement } from '@/types/profile';

interface AchievementsProps {
  achievements: Achievement[];
  isOwner: boolean;
  onAddAchievement?: () => void;
  onEditAchievement?: (achievement: Achievement) => void;
}

// Achievement type configs
const TYPE_CONFIG: Record<
  string,
  { icon: React.ReactNode; color: string; bgColor: string; borderColor: string }
> = {
  Hackathon: {
    icon: <Target className="w-5 h-5" />,
    color: 'text-purple-400',
    bgColor: 'from-purple-500/20 to-indigo-500/20',
    borderColor: 'border-purple-500/30',
  },
  Competition: {
    icon: <Trophy className="w-5 h-5" />,
    color: 'text-amber-400',
    bgColor: 'from-amber-500/20 to-orange-500/20',
    borderColor: 'border-amber-500/30',
  },
  Award: {
    icon: <Medal className="w-5 h-5" />,
    color: 'text-emerald-400',
    bgColor: 'from-emerald-500/20 to-teal-500/20',
    borderColor: 'border-emerald-500/30',
  },
  Scholarship: {
    icon: <Gift className="w-5 h-5" />,
    color: 'text-blue-400',
    bgColor: 'from-blue-500/20 to-cyan-500/20',
    borderColor: 'border-blue-500/30',
  },
  Recognition: {
    icon: <Sparkles className="w-5 h-5" />,
    color: 'text-rose-400',
    bgColor: 'from-rose-500/20 to-pink-500/20',
    borderColor: 'border-rose-500/30',
  },
};

export function Achievements({
  achievements,
  isOwner,
  onAddAchievement,
  onEditAchievement,
}: AchievementsProps) {
  const [selectedAchievement, setSelectedAchievement] = useState<Achievement | null>(null);

  if (achievements.length === 0 && !isOwner) {
    return null;
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'long',
      year: 'numeric',
    });
  };

  const formatFullDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      weekday: 'long',
      day: 'numeric',
      month: 'long',
      year: 'numeric',
    });
  };

  // Check if URL is an image
  const isImageUrl = (url: string) => {
    const imageExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.webp', '.bmp', '.svg'];
    const lowercaseUrl = url.toLowerCase();
    return imageExtensions.some(ext => lowercaseUrl.includes(ext)) || 
           lowercaseUrl.includes('/certificates/') ||
           lowercaseUrl.includes('storage.bunnycdn.com');
  };

  return (
    <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
          <Trophy className="w-5 h-5 text-amber-400" />
          Achievements
          <span className="text-sm font-normal text-gray-600 dark:text-neutral-400 ml-2">
            {achievements.length} achievements
          </span>
        </h2>

        {isOwner && onAddAchievement && (
          <Button
            onClick={onAddAchievement}
            className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white text-sm"
          >
            <Plus className="w-4 h-4 mr-1" />
            Add
          </Button>
        )}
      </div>

      {achievements.length === 0 ? (
        <div className="text-center py-8">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 dark:bg-neutral-800 rounded-full flex items-center justify-center">
            <Trophy className="w-8 h-8 text-gray-500 dark:text-neutral-500" />
          </div>
          <p className="text-gray-600 dark:text-neutral-400">No achievements added yet.</p>
          {isOwner && onAddAchievement && (
            <Button
              onClick={onAddAchievement}
              className="mt-4 bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
            >
              <Plus className="w-4 h-4 mr-2" />
              Add your first achievement
            </Button>
          )}
        </div>
      ) : (
        <div className="grid md:grid-cols-2 gap-4">
          {achievements.map((achievement, index) => {
            const config = TYPE_CONFIG[achievement.type] || TYPE_CONFIG.Award;
            return (
              <motion.div
                key={achievement.id}
                initial={{ opacity: 0, scale: 0.95 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ delay: index * 0.1 }}
                className={`group relative bg-gradient-to-br ${config.bgColor} rounded-lg border ${config.borderColor} p-4 hover:border-opacity-70 transition-colors`}
              >
                <div className="flex items-start gap-4">
                  {/* Icon */}
                  <div
                    className={`w-12 h-12 rounded-xl bg-gray-100 dark:bg-neutral-800/50 flex items-center justify-center flex-shrink-0 ${config.color}`}
                  >
                    {config.icon}
                  </div>

                  {/* Content */}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-start justify-between gap-2">
                      <div className="flex-1 min-w-0">
                        <h4 className="text-gray-900 dark:text-white font-semibold line-clamp-2">
                          {achievement.title}
                        </h4>
                        <p className="text-gray-600 dark:text-neutral-400 text-sm">
                          {achievement.organization}
                        </p>
                      </div>
                      <span
                        className={`px-2 py-0.5 rounded-full text-xs border ${config.borderColor} ${config.color} bg-neutral-900/50 flex-shrink-0`}
                      >
                        {achievement.type}
                      </span>
                    </div>

                    {/* Date */}
                    <div className="flex items-center gap-1 mt-2 text-xs text-gray-500 dark:text-neutral-500">
                      <Calendar className="w-3 h-3" />
                      {formatDate(achievement.date)}
                    </div>

                    {/* Description */}
                    {achievement.description && (
                      <p className="text-gray-600 dark:text-neutral-400 text-sm mt-2 line-clamp-2">
                        {achievement.description}
                      </p>
                    )}

                    {/* Certificate Link */}
                    {achievement.certificateUrl && (
                      <button
                        onClick={() => setSelectedAchievement(achievement)}
                        className="inline-flex items-center gap-1 mt-3 text-sm text-blue-400 hover:text-blue-300"
                      >
                        <Eye className="w-3 h-3" />
                        View Certificate
                      </button>
                    )}
                  </div>
                </div>

                {/* Edit Button */}
                {isOwner && onEditAchievement && (
                  <button
                    onClick={() => onEditAchievement(achievement)}
                    className="absolute top-3 right-3 opacity-0 group-hover:opacity-100 p-1.5 bg-neutral-700 text-gray-600 dark:text-neutral-400 rounded-lg hover:text-gray-900 dark:text-white transition-all"
                  >
                    <Edit2 className="w-3 h-3" />
                  </button>
                )}
              </motion.div>
            );
          })}
        </div>
      )}

      {/* Certificate Modal */}
      <Dialog.Root open={!!selectedAchievement} onOpenChange={(open) => !open && setSelectedAchievement(null)}>
        <AnimatePresence>
          {selectedAchievement && (
            <Dialog.Portal forceMount>
              <Dialog.Overlay asChild>
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                  className="fixed inset-0 bg-black/70 backdrop-blur-sm z-50"
                />
              </Dialog.Overlay>
              <Dialog.Content asChild>
                <motion.div
                  initial={{ opacity: 0, scale: 0.95, y: 20 }}
                  animate={{ opacity: 1, scale: 1, y: 0 }}
                  exit={{ opacity: 0, scale: 0.95, y: 20 }}
                  transition={{ type: 'spring', duration: 0.3 }}
                  className="fixed left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-3xl max-h-[90vh] overflow-y-auto bg-white dark:bg-neutral-900 rounded-2xl shadow-2xl z-50 border border-gray-200 dark:border-neutral-800"
                >
                  {/* Header */}
                  <div className="sticky top-0 bg-white dark:bg-neutral-900 border-b border-gray-200 dark:border-neutral-800 px-6 py-4 flex items-center justify-between">
                    <Dialog.Title className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                      {(() => {
                        const config = TYPE_CONFIG[selectedAchievement.type] || TYPE_CONFIG.Award;
                        return (
                          <>
                            <span className={config.color}>{config.icon}</span>
                            Certificate
                          </>
                        );
                      })()}
                    </Dialog.Title>
                    <Dialog.Close asChild>
                      <button
                        className="p-2 hover:bg-gray-100 dark:hover:bg-neutral-800 rounded-lg transition-colors"
                        aria-label="Close"
                      >
                        <X className="w-5 h-5 text-gray-500 dark:text-neutral-400" />
                      </button>
                    </Dialog.Close>
                  </div>

                  {/* Content */}
                  <div className="p-6">
                    {/* Certificate Image */}
                    {selectedAchievement.certificateUrl && isImageUrl(selectedAchievement.certificateUrl) ? (
                      <div className="mb-6 rounded-xl overflow-hidden border border-gray-200 dark:border-neutral-700 bg-gray-50 dark:bg-neutral-800">
                        <img
                          src={selectedAchievement.certificateUrl}
                          alt={`${selectedAchievement.title} certificate`}
                          className="w-full h-auto object-contain max-h-[50vh]"
                        />
                      </div>
                    ) : selectedAchievement.certificateUrl && (
                      <div className="mb-6 p-4 rounded-xl border border-gray-200 dark:border-neutral-700 bg-gray-50 dark:bg-neutral-800">
                        <a
                          href={selectedAchievement.certificateUrl}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="inline-flex items-center gap-2 text-blue-500 hover:text-blue-400"
                        >
                          <ExternalLink className="w-4 h-4" />
                          Open Certificate Link
                        </a>
                      </div>
                    )}

                    {/* Achievement Details */}
                    <div className={`p-5 rounded-xl bg-gradient-to-br ${(TYPE_CONFIG[selectedAchievement.type] || TYPE_CONFIG.Award).bgColor} border ${(TYPE_CONFIG[selectedAchievement.type] || TYPE_CONFIG.Award).borderColor}`}>
                      {/* Type Badge */}
                      <div className="flex items-center gap-2 mb-4">
                        <span className={`px-3 py-1 rounded-full text-sm border ${(TYPE_CONFIG[selectedAchievement.type] || TYPE_CONFIG.Award).borderColor} ${(TYPE_CONFIG[selectedAchievement.type] || TYPE_CONFIG.Award).color} bg-neutral-900/50`}>
                          {selectedAchievement.type}
                        </span>
                      </div>

                      {/* Title */}
                      <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
                        {selectedAchievement.title}
                      </h3>

                      {/* Organization */}
                      <div className="flex items-center gap-2 text-gray-600 dark:text-neutral-300 mb-3">
                        <Building className="w-4 h-4" />
                        <span className="font-medium">{selectedAchievement.organization}</span>
                      </div>

                      {/* Date */}
                      <div className="flex items-center gap-2 text-gray-500 dark:text-neutral-400 mb-4">
                        <Calendar className="w-4 h-4" />
                        <span>{formatFullDate(selectedAchievement.date)}</span>
                      </div>

                      {/* Description */}
                      {selectedAchievement.description && (
                        <div className="pt-4 border-t border-gray-200 dark:border-neutral-700">
                          <div className="flex items-center gap-2 text-gray-500 dark:text-neutral-400 mb-2">
                            <FileText className="w-4 h-4" />
                            <span className="text-sm font-medium">Description</span>
                          </div>
                          <p className="text-gray-700 dark:text-neutral-300 leading-relaxed">
                            {selectedAchievement.description}
                          </p>
                        </div>
                      )}
                    </div>
                  </div>
                </motion.div>
              </Dialog.Content>
            </Dialog.Portal>
          )}
        </AnimatePresence>
      </Dialog.Root>
    </Card>
  );
}

