'use client';

import { useState, useEffect, useCallback } from 'react';
import { motion } from 'framer-motion';
import * as Tabs from '@radix-ui/react-tabs';
import {
  FileText,
  Newspaper,
  Video,
  MessageSquare,
  Heart,
  Eye,
  Clock,
  Loader2,
} from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { getUserFeed } from '@/lib/api/profile';
import type { FeedItem, RecentActivity } from '@/types/profile';

interface ProfileFeedProps {
  userId: string;
  initialFeed?: RecentActivity;
}

type FilterType = 'all' | 'posts' | 'articles' | 'forum' | 'videos';

const TABS: { value: FilterType; label: string; icon: React.ReactNode }[] = [
  { value: 'all', label: 'All', icon: <FileText className="w-4 h-4" /> },
  { value: 'posts', label: 'Posts', icon: <MessageSquare className="w-4 h-4" /> },
  { value: 'articles', label: 'Articles', icon: <Newspaper className="w-4 h-4" /> },
  { value: 'videos', label: 'Reels', icon: <Video className="w-4 h-4" /> },
];

export function ProfileFeed({ userId, initialFeed }: ProfileFeedProps) {
  const [activeTab, setActiveTab] = useState<FilterType>('all');
  const [items, setItems] = useState<FeedItem[]>(initialFeed?.items || []);
  const [hasMore, setHasMore] = useState(initialFeed?.hasMore || false);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [loadingMore, setLoadingMore] = useState(false);

  // Fetch feed when tab changes
  useEffect(() => {
    const fetchFeed = async () => {
      setLoading(true);
      try {
        const response = await getUserFeed(userId, 1, 20, activeTab);
        setItems(response.items);
        setHasMore(response.hasMore);
        setPage(1);
      } catch (error) {
        console.error('Failed to fetch feed:', error);
      } finally {
        setLoading(false);
      }
    };

    // Only fetch if we don't have initial data or tab changed
    if (!initialFeed || activeTab !== 'all') {
      fetchFeed();
    }
  }, [userId, activeTab, initialFeed]);

  // Load more
  const loadMore = useCallback(async () => {
    if (loadingMore || !hasMore) return;

    setLoadingMore(true);
    try {
      const nextPage = page + 1;
      const response = await getUserFeed(userId, nextPage, 20, activeTab);
      setItems((prev) => [...prev, ...response.items]);
      setHasMore(response.hasMore);
      setPage(nextPage);
    } catch (error) {
      console.error('Failed to load more:', error);
    } finally {
      setLoadingMore(false);
    }
  }, [userId, page, activeTab, hasMore, loadingMore]);

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diff = now.getTime() - date.getTime();

    const minutes = Math.floor(diff / (1000 * 60));
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));

    if (minutes < 60) return `${minutes}m ago`;
    if (hours < 24) return `${hours}h ago`;
    if (days < 7) return `${days}d ago`;
    return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
  };

  const getContentTypeLabel = (type: string) => {
    const labels: Record<string, string> = {
      post: 'Post',
      article: 'Article',
      short_video: 'Reel',
      forum_question: 'Question',
      forum_answer: 'Answer',
    };
    return labels[type] || type;
  };

  const getContentTypeColor = (type: string) => {
    const colors: Record<string, string> = {
      post: 'text-blue-400 bg-blue-500/10 border-blue-500/30',
      article: 'text-emerald-400 bg-emerald-500/10 border-emerald-500/30',
      short_video: 'text-purple-400 bg-purple-500/10 border-purple-500/30',
      forum_question: 'text-amber-400 bg-amber-500/10 border-amber-500/30',
      forum_answer: 'text-rose-400 bg-rose-500/10 border-rose-500/30',
    };
    return colors[type] || 'text-gray-600 dark:text-neutral-400 bg-neutral-500/10 border-neutral-500/30';
  };

  return (
    <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
      <Tabs.Root value={activeTab} onValueChange={(v) => setActiveTab(v as FilterType)}>
        {/* Tabs */}
        <Tabs.List className="flex gap-1 p-1 bg-gray-100 dark:bg-neutral-800/50 rounded-lg mb-6">
          {TABS.map((tab) => (
            <Tabs.Trigger
              key={tab.value}
              value={tab.value}
              className={`flex items-center gap-2 px-4 py-2 rounded-md text-sm font-medium transition-colors ${
                activeTab === tab.value
                  ? 'bg-neutral-700 text-gray-900 dark:text-white'
                  : 'text-gray-600 dark:text-neutral-400 hover:text-gray-900 dark:text-white hover:bg-gray-200 dark:hover:bg-neutral-700/50'
              }`}
            >
              {tab.icon}
              {tab.label}
            </Tabs.Trigger>
          ))}
        </Tabs.List>

        {/* Content */}
        <Tabs.Content value={activeTab}>
          {loading ? (
            <div className="flex items-center justify-center py-12">
              <Loader2 className="w-8 h-8 text-gray-500 dark:text-neutral-500 animate-spin" />
            </div>
          ) : items.length === 0 ? (
            <div className="text-center py-12">
              <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 dark:bg-neutral-800 rounded-full flex items-center justify-center">
                <FileText className="w-8 h-8 text-gray-500 dark:text-neutral-500" />
              </div>
              <p className="text-gray-600 dark:text-neutral-400">No content yet.</p>
            </div>
          ) : (
            <div className="space-y-4">
              {items.map((item, index) => (
                <motion.div
                  key={item.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.05 }}
                  className="bg-gray-100 dark:bg-neutral-800/50 rounded-lg border border-gray-300 dark:border-neutral-700/50 p-4 hover:border-neutral-600 transition-colors"
                >
                  {/* Header */}
                  <div className="flex items-start justify-between mb-3">
                    <div className="flex items-center gap-2">
                      <span
                        className={`px-2 py-0.5 rounded-full text-xs border ${getContentTypeColor(
                          item.contentType
                        )}`}
                      >
                        {getContentTypeLabel(item.contentType)}
                      </span>
                      <span className="flex items-center gap-1 text-xs text-gray-500 dark:text-neutral-500">
                        <Clock className="w-3 h-3" />
                        {formatDate(item.createdAt)}
                      </span>
                    </div>
                  </div>

                  {/* Title (for articles/questions) */}
                  {item.title && (
                    <h4 className="text-gray-900 dark:text-white font-semibold mb-2">{item.title}</h4>
                  )}

                  {/* Content */}
                  <p className="text-gray-700 dark:text-neutral-300 text-sm line-clamp-3">
                    {item.content}
                  </p>

                  {/* Tags */}
                  {item.tags && item.tags.length > 0 && (
                    <div className="flex flex-wrap gap-1 mt-3">
                      {item.tags.map((tag) => (
                        <span
                          key={tag}
                          className="px-2 py-0.5 bg-neutral-700/50 text-gray-600 dark:text-neutral-400 rounded text-xs"
                        >
                          #{tag}
                        </span>
                      ))}
                    </div>
                  )}

                  {/* Images */}
                  {item.images && item.images.length > 0 && (
                    <div className="mt-3 flex gap-2 overflow-x-auto">
                      {item.images.slice(0, 3).map((img, i) => (
                        <img
                          key={i}
                          src={img}
                          alt=""
                          className="h-20 w-auto rounded-lg object-cover"
                        />
                      ))}
                      {item.images.length > 3 && (
                        <div className="h-20 w-20 rounded-lg bg-neutral-700 flex items-center justify-center text-gray-600 dark:text-neutral-400 text-sm">
                          +{item.images.length - 3}
                        </div>
                      )}
                    </div>
                  )}

                  {/* Question reference for answers */}
                  {item.questionTitle && (
                    <div className="mt-3 p-2 bg-gray-100 dark:bg-neutral-800 rounded-lg border border-gray-300 dark:border-neutral-700">
                      <p className="text-xs text-gray-500 dark:text-neutral-500">Answered:</p>
                      <p className="text-sm text-gray-700 dark:text-neutral-300">{item.questionTitle}</p>
                    </div>
                  )}

                  {/* Engagement Stats */}
                  <div className="flex items-center gap-4 mt-4 text-sm text-gray-500 dark:text-neutral-500">
                    {item.likesCount !== undefined && (
                      <span className="flex items-center gap-1">
                        <Heart className="w-4 h-4" />
                        {item.likesCount}
                      </span>
                    )}
                    {item.commentsCount !== undefined && (
                      <span className="flex items-center gap-1">
                        <MessageSquare className="w-4 h-4" />
                        {item.commentsCount}
                      </span>
                    )}
                    {item.viewsCount !== undefined && (
                      <span className="flex items-center gap-1">
                        <Eye className="w-4 h-4" />
                        {item.viewsCount}
                      </span>
                    )}
                  </div>
                </motion.div>
              ))}

              {/* Load More */}
              {hasMore && (
                <div className="flex justify-center pt-4">
                  <button
                    onClick={loadMore}
                    disabled={loadingMore}
                    className="px-4 py-2 bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 rounded-lg hover:bg-gray-200 dark:hover:bg-neutral-700 transition-colors disabled:opacity-50 flex items-center gap-2"
                  >
                    {loadingMore ? (
                      <>
                        <Loader2 className="w-4 h-4 animate-spin" />
                        Loading...
                      </>
                    ) : (
                      'Load More'
                    )}
                  </button>
                </div>
              )}
            </div>
          )}
        </Tabs.Content>
      </Tabs.Root>
    </Card>
  );
}

