'use client';

import { useState, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import * as Dialog from '@radix-ui/react-dialog';
import {
  X,
  Trophy,
  Target,
  Medal,
  Gift,
  Sparkles,
  Loader2,
  Calendar,
  Building,
  Link as LinkIcon,
  FileText,
  Upload,
  ImageIcon,
  Trash2,
} from 'lucide-react';
import { Button } from '@/components/ui/Button';
import { createAchievement } from '@/lib/api/profile';
import { uploadCertificate } from '@/lib/api/posts';
import type { Achievement, AchievementInput } from '@/types/profile';

interface AddAchievementModalProps {
  isOpen: boolean;
  onClose: () => void;
  onAchievementAdded: (achievement: Achievement) => void;
}

// Achievement type options
const ACHIEVEMENT_TYPES = [
  { value: 'Hackathon', label: 'Hackathon', icon: <Target className="w-5 h-5" />, color: 'text-purple-400 bg-purple-500/20 border-purple-500/30' },
  { value: 'Competition', label: 'Competition', icon: <Trophy className="w-5 h-5" />, color: 'text-amber-400 bg-amber-500/20 border-amber-500/30' },
  { value: 'Award', label: 'Award', icon: <Medal className="w-5 h-5" />, color: 'text-emerald-400 bg-emerald-500/20 border-emerald-500/30' },
  { value: 'Scholarship', label: 'Scholarship', icon: <Gift className="w-5 h-5" />, color: 'text-blue-400 bg-blue-500/20 border-blue-500/30' },
  { value: 'Recognition', label: 'Recognition', icon: <Sparkles className="w-5 h-5" />, color: 'text-rose-400 bg-rose-500/20 border-rose-500/30' },
] as const;

export function AddAchievementModal({
  isOpen,
  onClose,
  onAchievementAdded,
}: AddAchievementModalProps) {
  const [loading, setLoading] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Form state
  const [title, setTitle] = useState('');
  const [type, setType] = useState<AchievementInput['type']>('Award');
  const [organization, setOrganization] = useState('');
  const [date, setDate] = useState('');
  const [description, setDescription] = useState('');
  const [certificateUrl, setCertificateUrl] = useState('');
  const [certificateFile, setCertificateFile] = useState<File | null>(null);
  const [certificatePreview, setCertificatePreview] = useState<string | null>(null);
  const [uploadMethod, setUploadMethod] = useState<'url' | 'file'>('file');
  
  const fileInputRef = useRef<HTMLInputElement>(null);

  const resetForm = () => {
    setTitle('');
    setType('Award');
    setOrganization('');
    setDate('');
    setDescription('');
    setCertificateUrl('');
    setCertificateFile(null);
    setCertificatePreview(null);
    setUploadMethod('file');
    setError(null);
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      // Validate file type
      if (!file.type.startsWith('image/')) {
        setError('Please select an image file');
        return;
      }
      // Validate file size (max 5MB)
      if (file.size > 5 * 1024 * 1024) {
        setError('File size should be less than 5MB');
        return;
      }
      
      setCertificateFile(file);
      setCertificatePreview(URL.createObjectURL(file));
      setError(null);
    }
  };

  const handleRemoveFile = () => {
    if (certificatePreview) {
      URL.revokeObjectURL(certificatePreview);
    }
    setCertificateFile(null);
    setCertificatePreview(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handleClose = () => {
    resetForm();
    onClose();
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!title.trim()) {
      setError('Title is required');
      return;
    }
    if (!organization.trim()) {
      setError('Organization is required');
      return;
    }
    if (!date) {
      setError('Date is required');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      let finalCertificateUrl = certificateUrl.trim();

      // Upload certificate file if selected
      if (uploadMethod === 'file' && certificateFile) {
        setUploading(true);
        try {
          const result = await uploadCertificate(certificateFile);
          finalCertificateUrl = result.certificateUrl;
        } catch (uploadErr: any) {
          console.error('Failed to upload certificate:', uploadErr);
          setError('Failed to upload certificate image. Please try again.');
          setLoading(false);
          setUploading(false);
          return;
        }
        setUploading(false);
      }

      const achievementData: AchievementInput = {
        title: title.trim(),
        type,
        organization: organization.trim(),
        date,
        description: description.trim() || undefined,
        certificateUrl: finalCertificateUrl || undefined,
      };

      const achievement = await createAchievement(achievementData);
      onAchievementAdded(achievement);
      handleClose();
    } catch (err: any) {
      console.error('Failed to add achievement:', err);
      setError(err.response?.data?.error || err.message || 'Failed to add achievement');
    } finally {
      setLoading(false);
      setUploading(false);
    }
  };

  return (
    <Dialog.Root open={isOpen} onOpenChange={(open) => !open && handleClose()}>
      <AnimatePresence>
        {isOpen && (
          <Dialog.Portal forceMount>
            <Dialog.Overlay asChild>
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50"
              />
            </Dialog.Overlay>
            <Dialog.Content asChild>
              <motion.div
                initial={{ opacity: 0, scale: 0.95, y: 20 }}
                animate={{ opacity: 1, scale: 1, y: 0 }}
                exit={{ opacity: 0, scale: 0.95, y: 20 }}
                transition={{ type: 'spring', duration: 0.3 }}
                className="fixed left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-lg max-h-[90vh] overflow-y-auto bg-white dark:bg-neutral-900 rounded-2xl shadow-2xl z-50 border border-gray-200 dark:border-neutral-800"
              >
                {/* Header */}
                <div className="sticky top-0 bg-white dark:bg-neutral-900 border-b border-gray-200 dark:border-neutral-800 px-6 py-4 flex items-center justify-between">
                  <Dialog.Title className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                    <Trophy className="w-5 h-5 text-amber-400" />
                    Add Achievement
                  </Dialog.Title>
                  <Dialog.Close asChild>
                    <button
                      className="p-2 hover:bg-gray-100 dark:hover:bg-neutral-800 rounded-lg transition-colors"
                      aria-label="Close"
                    >
                      <X className="w-5 h-5 text-gray-500 dark:text-neutral-400" />
                    </button>
                  </Dialog.Close>
                </div>

                {/* Form */}
                <form onSubmit={handleSubmit} className="p-6 space-y-6">
                  {/* Error Message */}
                  {error && (
                    <div className="p-3 bg-red-500/10 border border-red-500/30 rounded-lg text-red-400 text-sm">
                      {error}
                    </div>
                  )}

                  {/* Achievement Type */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      Type
                    </label>
                    <div className="grid grid-cols-5 gap-2">
                      {ACHIEVEMENT_TYPES.map((t) => (
                        <button
                          key={t.value}
                          type="button"
                          onClick={() => setType(t.value)}
                          className={`flex flex-col items-center gap-1 p-3 rounded-lg border transition-all ${
                            type === t.value
                              ? t.color + ' border-2'
                              : 'bg-gray-50 dark:bg-neutral-800 border-gray-200 dark:border-neutral-700 hover:border-gray-300 dark:hover:border-neutral-600'
                          }`}
                        >
                          <span className={type === t.value ? '' : 'text-gray-500 dark:text-neutral-400'}>
                            {t.icon}
                          </span>
                          <span className={`text-xs ${type === t.value ? '' : 'text-gray-600 dark:text-neutral-400'}`}>
                            {t.label}
                          </span>
                        </button>
                      ))}
                    </div>
                  </div>

                  {/* Title */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      Title *
                    </label>
                    <input
                      type="text"
                      value={title}
                      onChange={(e) => setTitle(e.target.value)}
                      placeholder="e.g., 1st Place Winner, Best Innovation Award"
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                    />
                  </div>

                  {/* Organization */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <Building className="w-4 h-4 inline mr-1" />
                      Organization *
                    </label>
                    <input
                      type="text"
                      value={organization}
                      onChange={(e) => setOrganization(e.target.value)}
                      placeholder="e.g., Google, MIT, TechCrunch"
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                    />
                  </div>

                  {/* Date */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <Calendar className="w-4 h-4 inline mr-1" />
                      Date *
                    </label>
                    <input
                      type="date"
                      value={date}
                      onChange={(e) => setDate(e.target.value)}
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                    />
                  </div>

                  {/* Description */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <FileText className="w-4 h-4 inline mr-1" />
                      Description (Optional)
                    </label>
                    <textarea
                      value={description}
                      onChange={(e) => setDescription(e.target.value)}
                      placeholder="Describe your achievement..."
                      rows={3}
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500 resize-none"
                    />
                  </div>

                  {/* Certificate Upload */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2">
                      <ImageIcon className="w-4 h-4 inline mr-1" />
                      Certificate/Proof (Optional)
                    </label>
                    
                    {/* Upload Method Toggle */}
                    <div className="flex gap-2 mb-3">
                      <button
                        type="button"
                        onClick={() => setUploadMethod('file')}
                        className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium transition-all ${
                          uploadMethod === 'file'
                            ? 'bg-blue-600 text-white'
                            : 'bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400 hover:bg-gray-200 dark:hover:bg-neutral-700'
                        }`}
                      >
                        <Upload className="w-4 h-4 inline mr-1" />
                        Upload Image
                      </button>
                      <button
                        type="button"
                        onClick={() => setUploadMethod('url')}
                        className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium transition-all ${
                          uploadMethod === 'url'
                            ? 'bg-blue-600 text-white'
                            : 'bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400 hover:bg-gray-200 dark:hover:bg-neutral-700'
                        }`}
                      >
                        <LinkIcon className="w-4 h-4 inline mr-1" />
                        Paste URL
                      </button>
                    </div>

                    {uploadMethod === 'file' ? (
                      <div>
                        {certificatePreview ? (
                          <div className="relative rounded-lg overflow-hidden border border-gray-200 dark:border-neutral-700">
                            <img
                              src={certificatePreview}
                              alt="Certificate preview"
                              className="w-full max-h-64 object-contain bg-gray-50 dark:bg-neutral-800"
                            />
                            <button
                              type="button"
                              onClick={handleRemoveFile}
                              className="absolute top-2 right-2 p-2 bg-red-500 hover:bg-red-600 text-white rounded-full transition-colors"
                            >
                              <Trash2 className="w-4 h-4" />
                            </button>
                            <div className="absolute bottom-2 left-2 px-2 py-1 bg-black/60 text-white text-xs rounded">
                              {certificateFile?.name}
                            </div>
                          </div>
                        ) : (
                          <div
                            onClick={() => fileInputRef.current?.click()}
                            className="border-2 border-dashed border-gray-300 dark:border-neutral-600 rounded-lg p-8 text-center cursor-pointer hover:border-blue-500 dark:hover:border-blue-500 transition-colors"
                          >
                            <Upload className="w-10 h-10 mx-auto text-gray-400 dark:text-neutral-500 mb-2" />
                            <p className="text-sm text-gray-600 dark:text-neutral-400">
                              Click to upload certificate image
                            </p>
                            <p className="text-xs text-gray-400 dark:text-neutral-500 mt-1">
                              PNG, JPG up to 5MB
                            </p>
                          </div>
                        )}
                        <input
                          ref={fileInputRef}
                          type="file"
                          accept="image/*"
                          onChange={handleFileSelect}
                          className="hidden"
                        />
                      </div>
                    ) : (
                      <input
                        type="url"
                        value={certificateUrl}
                        onChange={(e) => setCertificateUrl(e.target.value)}
                        placeholder="https://..."
                        className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800 border border-gray-200 dark:border-neutral-700 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500"
                      />
                    )}
                  </div>

                  {/* Submit Button */}
                  <div className="flex justify-end gap-3 pt-4">
                    <Button
                      type="button"
                      onClick={handleClose}
                      disabled={loading || uploading}
                      className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
                    >
                      Cancel
                    </Button>
                    <Button
                      type="submit"
                      disabled={loading || uploading}
                      className="bg-blue-600 hover:bg-blue-700 text-white"
                    >
                      {uploading ? (
                        <>
                          <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                          Uploading...
                        </>
                      ) : loading ? (
                        <>
                          <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                          Adding...
                        </>
                      ) : (
                        'Add Achievement'
                      )}
                    </Button>
                  </div>
                </form>
              </motion.div>
            </Dialog.Content>
          </Dialog.Portal>
        )}
      </AnimatePresence>
    </Dialog.Root>
  );
}
